import React,{Component} from 'react';
import './AppFoot.css';
import Utils from '../Utils';

export default class AppFoot extends Component {

    renderPhone() {
        return (
            <div className="AppFoot">
                <div className="PhoneFootLeft">
                    <h2 style={{marginLeft:20}}></h2>
                    <div className="FootLine" style={{marginLeft:20,marginRight:20}}/>
                    <p className="FootDesc" style={{marginLeft:20,marginRight:20}}>
                        18539290519
                        <br/>
                        QQ1010314912
                        <br/>
                        
                    </p>
                </div>

                <div className="PhoneFootRight">
                    <h2 style={{marginLeft:20}}></h2>
                    <div className="FootLine" style={{marginLeft:20,marginRight:20}}/>
                    <p className="FootDesc" style={{marginLeft:20,marginRight:20}}>+ </p>
                </div>

                <div className="FootLine"/>

                <p className="FootName" style={{fontSize:13}}>COPYRIGHT (c) 2017  All RIGHTS RESERVED</p>

            </div>
        );
    }

    render() {

        if (Utils.IsPhone()) {
            return this.renderPhone();
        }
        return(
            <div className="AppFoot">
                <div className="FootTop">
                    <div className="FootLeft">
                        <h2 style={{marginLeft:40}}></h2>
                        <div className="FootLine"/>
                        <p className="FootDesc">
                            18539290519
                            <br/>
                            QQ1010314912
                            <br/>
                            
                        </p>
                    </div>

                    <div className="FootRight">
                        <h2 style={{marginLeft:40}}></h2>
                        <div className="FootLine"/>
                        <p className="FootDesc">+ </p>
                    </div>
                </div>

                <div className="FootLine"/>

                <p className="FootName">COPYRIGHT (c) 2015-2017  All RIGHTS RESERVED</p>
            </div>
        );
    }
}



